import React from 'react';

const SearchBox = ({ searchfield, searchChange }) => {
	return (
		<div className='pa2'>
		<input
			className='tc pa3 ba b--green bg-lightest-blue br-pill' 
			type='search' 
			placeholder='search pets...'
			onChange={searchChange} 
		/>
		</div>

	);
}

export default SearchBox; 